import React from 'react';
import '../assets/styles/skills.scss'
export default ()=>{
  const techs = [
    {src: 'svg/react.svg', style: {}},
    {src:'svg/vuejs.svg',style: {}},
    {src:'svg/sass.svg',style: {}},
    {src:'svg/stylus.svg',style: {}},
    {src:'svg/nodejs.svg',style: {transform: 'scale(1.6)'}},
    {src:'svg/express.svg',style: {}},
    {src:'png/socketio.png',style: {transform: 'scale(1.6)', marginTop: 30}},
    {src:'svg/mysql.svg',style: {}},
    {src:'svg/mongodb.svg',style: {}}
  ];
  return(
    <div className="SkillsComponent">
      <div className="container">
        <p className="title is-4 has-text-weight-bold" style={{marginLeft: 10}}>Mis Skills</p>
        <div className="columns">
          {
            techs.map((tech, index)=>(
              <div className="column" key={index}>
                <div className="contenedor wow fadeInDown" data-wow-delay={`${.1*index}s`}>
                  <img src={require(`../assets/${tech.src}`)} style={tech.style}  alt=""/>
                </div>
              </div>
            ))
          }
          
        </div>
      </div>
    </div>
  );
}